// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import 'bootstrap';   //bootstrapjquery
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
//npm

import App from './App';
import router from './router'; //router/index.js
import './bus';  //bus
import currencyFilter from './filters/currency'; //currency
import DateFilter from './filters/Date';
import VeeValidate from 'vee-validate';
import zhTW_Validate from 'vee-validate/dist/locale/zh_TW';  //
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css';
// 


axios.defaults.withCredentials = true;  //truecookie
Vue.config.productionTip = false;
Vue.use(VueAxios, axios); //
Vue.component('Loading', Loading); //Vue Loading Overlay
Vue.filter('currency',currencyFilter);  //Vue.filter('',import)
Vue.filter('Date',DateFilter); //
Vue.use(VeeValidate);
VeeValidate.Validator.localize('zh_TW',zhTW_Validate); //
Vue.use(VueAwesomeSwiper); //Swiper

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
});

router.beforeEach((to, from, next) => {
  console.log('to', to, 'from', from, 'next', next);
  if(to.meta.requiresAuth) {  //true
   const api = `${process.env.API_PATH}/api/user/check`;   //
          axios.post(api).then((response) => {  //loginvm.$httpaxios
          console.log(response.data);
          if(response.data.success){ //
            next();
          }else{
            next({
              path: '/Login',  //
            });
          }
      });
  }else{
    next(); //requiresAuth != trueHelloWorld
  }
});


