<template>
<div>
    <!-- Vue Loading Overlay -->
    <loading :active.sync="isLoading"></loading> 
    <div class="text-right mt-4">
        <button class="btn btn-outline-primary" @click="openModals(true)"></button>
    </div>
    <table class="table mt-4">
        <thead>
            <tr>
                <th width="120"></th>
                <th></th>
                <th width="120"></th>
                <th width="120"></th>
                <th width="120">/</th>
                <th width="125"></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in products" :key="item.id">
                <td>{{ item.category }}</td>
                <td>{{ item.title }}</td>
                <td class="text-right">
                    {{ item.origin_price | currency}}
                </td>
                <td class="text-right">
                    {{ item.price | currency}}
                </td>
                <td class="text-center">
                    <!-- is_enabled == 1  -->
                    <span v-if="item.is_enabled" class="text-success"></span> 
                    <span v-else></span>
                </td>
                <td>
                    <!-- @click="openModals(false, item)"  item-->
                    <button class="btn btn-outline-primary btn-sm" @click="openModals(false, item)"></button>
                    <button class="btn btn-outline-danger btn-sm" @click="deletModals(item)"></button>
                </td>
            </tr>
        </tbody>
    </table>
     <!-- Pagination -->
     <!-- userpage$emitgetproductsfunction -->
     <!-- :paginationprops -->
     <pagination :getpagination="pagination" @userpage="getproducts"></pagination> 
    <!-- Model -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image"></label>
                        <input type="text" class="form-control" id="image"
                            placeholder="" v-model="tempProduct.imageUrl">
                        </div>
                        <div class="form-group">
                        <label for="customFile"> 
                            <i class="fas fa-spinner fa-pulse" v-if="status.fileUploading"></i>
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files" @change="uploadFile">
                        </div>
                        <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                        class="img-fluid" alt="" :src="tempProduct.imageUrl">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title"></label>
                        <input type="text" class="form-control" id="title"
                            placeholder="" v-model="tempProduct.title">
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category"></label>
                            <input type="text" class="form-control" id="category"
                            placeholder="" v-model="tempProduct.category">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="unit" class="form-control" id="unit"
                            placeholder="" v-model="tempProduct.unit">
                        </div>
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price"></label>
                            <input type="number" class="form-control" id="origin_price"
                            placeholder="" v-model="tempProduct.origin_price">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="number" class="form-control" id="price"
                            placeholder="" v-model="tempProduct.price">
                        </div>
                        </div>
                        <hr>

                        <div class="form-group">
                        <label for="description"></label>
                        <textarea type="text" class="form-control" id="description"
                            placeholder="" v-model="tempProduct.description"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content"></label>
                        <textarea type="text" class="form-control" id="content"
                            placeholder="" v-model="tempProduct.content"></textarea>
                        </div>
                        <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox"
                            id="is_enabled" v-model="tempProduct.is_enabled" 
                            :true-value="1" :false-value="0"> 
                            <label class="form-check-label" for="is_enabled">
                            
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="updataProduct"></button>
                </div>
                </div>
            </div>
            </div>
            <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger" @click="deletProduct"
                    ></button>
                </div>
                </div>
            </div>
        </div>
</div>
</template>

<script>
import $ from 'jquery';    //$jquery
// window.$ = $;  //$$main.js
import pagination from '../pagination';

export default {
    data() {
        return {
            products: [],  //API
            tempProduct: {},  //HTMLtempProduct,""
            isNew: false,
            isLoading: false, //trueVue Loading Overlay
            status: {  //icon
                fileUploading: false,
            },
            pagination: {},
        };
    },
    components: {
        pagination,
    },
    methods: {
        getproducts(page = 1) { //page=11
            const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;//'https://vue-course-api.hexschool.io/api/tedcode/products'//;  //:api_path = api id
            const vm = this;  //vm.products
            vm.isLoading = true;
            //API and api path ${javascript(return)}
            // console.log(process.env.API_PATH, process.env.CUSTOMPATH); //dev.envAPI and custompath
            this.$http.get(api).then((response) => {
            vm.products = response.data.products;  //vm.products
            // console.log(response.data);
            vm.isLoading = false;
             vm.pagination = response.data.pagination;
            //  console.log(vm.pagination);
            });
        },
        openModals(isNew, item) {  //API
            if(isNew) {
                this.tempProduct = {};  
                this.isNew = true;
            }else {
                this.tempProduct = Object.assign({}, item);  //itemtempProductitem
                this.isNew = false;
            }
            $('#productModal').modal('show');  //
        },
        updataProduct() {  //
            let api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let httpMethod = 'post';
            const vm = this;  
            if(!vm.isNew) {  //
                api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                httpMethod = 'put';  //put
            }
            this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {  //this.$http[httpMethod] = this.$http.get(api) []
            console.log(response.data);
                if(response.data.success) {  //
                    $('#productModal').modal('hide');
                    vm.getproducts();  //
                }else {
                    $('#productModal').modal('hide');
                    vm.getproducts();
                    console.log('');
                };
            });
        },
        deletModals(item) {
            this.tempProduct = item;  //
            // console.log(item);
        $('#delProductModal').modal('show');
        },
        deletProduct() {
            const vm = this; 
            const api = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;  //apiid
            this.$http.delete(api).then((response) => {
                console.log(response);
                if(response.data.success) {
                    $('#delProductModal').modal('hide');
                    vm.getproducts();
                    this.$bus.$emit('message:push', response.data.message);
                    console.log('');
                }else {
                    $('#delProductModal').modal('hide');
                    vm.getproducts();
                    console.log('');
                }
            });
        },
        uploadFile() {
            // console.log(this);
            const uploadedFile = this.$refs.files.files[0]; //
            const vm = this;
            vm.status.fileUploading = true;  //
            const formdata = new FormData(); //formdata
            formdata.append('file-to-upload', uploadedFile); //formdata     append('', )
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            this.$http.post(url, formdata, {   //
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            }).then((response) => {
                // console.log(response.data);
                vm.status.fileUploading = false;  //
                if(response.data.success) {
                    // vm.tempProduct.imageUrl = response.data.imageUrl;
                    // console.log(vm.tempProduct);
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl); //
                }else {
                    this.$bus.$emit('message:push', response.data.message, 'danger');   //$on
                    //1. Products.vue  response.data.success  $emit  messsage:push 
                    // response.data.message(alert )  danger(alert )
                    //2. messsage:push  updateMessage(message, status)  updateMessage(message, status) 
                }
            });
        },  
    },
    created() {
        this.getproducts();
    },
}
</script>


