<template>
    <div>
        <loading :active.sync="isLoading"></loading> 
        <table class="table mt-4 mx-auto" style="width: 500px;" v-if="Cart.total !== 0">
            <thead style="width: 500px;">
                <tr>
                    <th width="100"></th>
                    <th></th>
                    <th width="100"></th>
                    <th width="100"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in Cart.carts" :key="item.id">
                    <td>
                        <button class="btn btn-outline-danger btn-sm" @click="deleteCar(item.id)">
                            <i class="far fa-trash-alt"></i>
                        </button>
                    </td>
                    <td>
                        {{ item.product.title }}
                        <div class="text-success" v-if="item.coupon">
                            
                        </div>
                    </td>
                    <td>{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="text-right">
                        {{ item.final_total | currency}}
                    </td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                    <!--  -->
                    <td colspan="3" class="text-right"></td>
                    <td class="text-right">{{ Cart.total }}</td>
                </tr>
                <!-- final_totaltotal-->
                <tr class="text-primary" v-if="Cart.final_total !== Cart.total">
                    <td colspan="3" class="text-right"></td>
                    <td class="text-right">{{ Cart.final_total }}</td>
                </tr>
            </tfoot>
        </table>
        <table class="table mx-auto" style="width: 500px; font-size: 14px" v-if="Cart.total !== 0">
            <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control " placeholder="" aria-describedby="basic-addon2" v-model="couponcode">
                <div class="input-group-append">
                    <button class="input-group-text btn btn-outline-secondary" id="basic-addon2" @click="addCouponCode"></button>
                </div>
            </div>
        </table>
        <!--  -->
        <div class="my-5 row justify-content-center" @submit.prevent="createdOrder">
            <form class="col-md-6">
                <div class="form-group">
                <label for="useremail">Email</label>
                <!-- inputrequiredChrome -->
                <input type="email" class="form-control" name="email" id="useremail"
                    v-model="form.user.email" placeholder=" Email" v-validate="'required|email'" 
                    :class="{'is-invalid' : errors.has('email')}" required>
                <span class="text-danger" v-if="errors.first('email')">{{ errors.first('email') }}</span>
                </div>
            
                <div class="form-group">
                <label for="username"></label>
                <input type="text" class="form-control" name="name" id="username"
                    v-model="form.user.name" placeholder="" 
                    v-validate="'required'" :class="{'is-invalid' : errors.has('name')}" required>
                <span class="text-danger" v-if="errors.has('name')"></span>
                </div>
            
                <div class="form-group">
                <label for="usertel"></label>
                <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="" name="tel" 
                v-validate="'required'" :class="{'is-invalid' : errors.has('tel')}" required>
                <span v-if="errors.has('tel')" class="text-danger"></span>
                </div>
            
                <div class="form-group">
                <label for="useraddress"></label>
                <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                    placeholder="" v-validate="'required'" :class="{'is-invalid' : errors.has('address')}" required>
                <span class="text-danger" v-if="errors.has('address')"></span>
                </div>
            
                <div class="form-group">
                <label for="comment"></label>
                <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                <button class="btn btn-danger"></button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    data() {
        return {
            status: {
                loading: {},
            },
            isLoading: false, 
            Cart: {},
            couponcode: '',
            form: {   //
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',
            },
        };
    },
    methods: {
        addtoCar(id, qty) {
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this;  
            vm.status.loading = id;
            const car = {
                product_id: id,
                qty  //
            };
            this.$http.post(url, { data:car }).then((response) => {
                // console.log(response);
                vm.status.loading = "";
                $('#productModal').modal('hide');
                this.gettoCar();
            });
        },
        gettoCar() {   //
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this;  
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.Cart = response.data.data;
                // console.log(response);
                vm.isLoading = false;
            });
        },
        deleteCar(id) {  //id
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            const vm = this;  
            vm.isLoading = true;
            this.$http.delete(url).then((response) => {
                // console.log(response.data);
                vm.isLoading = false;
                this.gettoCar();  //
            });
        },
        addCouponCode() {
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const vm = this;  
            const coupon = {
                code: vm.couponcode,  //inputpostAPI
            };
            vm.isLoading = true;
            this.$http.post(url, {data: coupon}).then((response) => {  //inputpostAPI
                console.log(response.data);
                vm.isLoading = false;
                this.gettoCar();  //
            });
        },
        createdOrder() {
            const url = `${process.env.API_PATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this;  
            const order = vm.form;
            // vm.isLoading = true;
            this.$validator.validate().then((valid) => {
                if (valid) {
                    this.$http.post(url, {data: order}).then((response) => {  //formapi
                        console.log('',response);
                        vm.isLoading = false;
                        if(response.data.success) {
                            vm.$router.push(`/Customer_checkout/${response.data.orderId}`); //
                        }
                    });
                }else {
                    console.log("");
                }
            });
        },
    },
    created() {
        this.gettoCar();
        // const vm = this;
        // vm.$bus.$on('cart', (id, qty=1)=> {  
        // vm.addtoCar(id, qty);
        // });
    },
};
</script>


